import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import styles from './style.module.css';


export const PDFView = () => {

    const showProjects = useSelector((state: any) => state.Main.showProjects);
    
    window.onbeforeprint = () => {
        document.getElementById("live_version_info")!.style.display = showProjects ? "none" : "block"
        document.getElementById("side_box")!.style.display = "none"
        document.getElementById("live_version_link")!.style.display = showProjects ? "initial" : "none"
    }
    window.onafterprint = () => {
        document.getElementById("live_version_info")!.style.display = "none"
        document.getElementById("side_box")!.style.display = "block"
        document.getElementById("live_version_link")!.style.display = "none"
    }

    return (
        <div>
            <div className={styles.live_version_info} id="live_version_info">
                <i className="material-icons">picture_as_pdf</i>
                <p>
                    Projects is not included in this PDF.<br/>
                    To view projects, go to live version<br/>
                    <span>Live Version: <a href='/' target='_blank'>{document.URL.split("//")[1].replace("/", "")}</a></span>
                </p>
                <p>
                    But if you steal preferred PDF with projects included (size: 28 MB!)<br/>
                    <span>Here is Full Version PDF: <a href='/pdf/Mohammad-Fakhrpour-Resume-FullVersion.pdf' target='_blank'>View</a></span>
                </p>
            </div>
        </div>
    )    
}