import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { ActionMain } from '../../actions';
import { Company } from '../../apis/models/company-list';
import { Project } from '../../apis/models/project-list';
import { importAll, openInNewTab } from '../../helpers/utility';
import styles from './style.module.css';

interface SelectedItem { project: string, item: string }
interface BigView { project: string, isBig: boolean }

export const Projects = ({ list, companies }: { list: Project[], companies: Company[] }) => {

    const dispatch = useDispatch();

    const projectImages = importAll(require.context('../../assets/project-images', false, /\.*/))
    const projectVideos = importAll(require.context('../../assets/project-videos', false, /\.*/))
    const [ selectedItems, setSelectedItems ] = useState<SelectedItem[]>([]);
    const [ bigViewProjects, setBigViewProjects ] = useState<BigView[]>([]);
    const showProjects = useSelector((state: any) => state.Main.showProjects);

    useEffect(() => {
        const selectedItemList: SelectedItem[] = [];
        const bigViewList: BigView[] = [];
        list.forEach(x => {
            selectedItemList.push({
                project: x.title,
                item: x.videos && x.videos.length ? x.videos[0] : (x.images && x.images.length ? x.images[0] : "")
            })
            bigViewList.push({
                project: x.title,
                isBig: false
            })
        })
        setSelectedItems(selectedItemList);
        setBigViewProjects(bigViewList);
    }, [list])

    useEffect(() => {
        companies.forEach(company => {
            company.count = list.filter(y => y.companies.find(a => a == company.name))?.length
        })

    }, [companies])

    const changeSelectedItem = (project: string, item: string) => {
        const selected = selectedItems.find(x => x.project == project);
        if (selected) selected.item = item;
        setSelectedItems([...selectedItems]);
    }

    const getSelectedItem = (project: string) => {
        const selected = selectedItems.find(x => x.project == project);
        return selected ? selected.item : "";
    }

    const getImage = (image: string) => {
        const obj = projectImages[image];
        return obj ? obj.default : "";
    }

    const getVideo = (video: string) => {
        const obj = projectVideos[video];
        return obj ? obj.default : "";
    }

    const isInBigViewMode = (project: string) => {
        const obj = bigViewProjects.find(x => x.project == project);
        return obj?.isBig;
    }

    const changeBigViewMode = (project: string, value: boolean) => {
        const obj = bigViewProjects.find(x => x.project == project);
        if (obj) obj.isBig = value;
        setBigViewProjects([...bigViewProjects]);
    }

    const showProjectList = () => {
        dispatch(ActionMain.showProjects(true))
    }


    return (
        <div className={`${styles.bottom_part} ${styles.parts} ${styles.projects}`} id="projects">

            {/* <div className={styles.filter_container}>

                <div className={styles.filter_title}>filter by company</div>
                
                <div className={styles.filter_items}>
                    {companies.map(company => 
                    <div className={styles.filter_company_item}>
                        <div className={styles.filter_company_item_name}>{company.name}</div>
                        <div className={styles.filter_company_item_count}>{company.count}</div>
                    </div>
                    )}
                </div>

            </div> */}

            <div className={styles.part_title_container}>
                <span className={styles.part_title}>Hands-On Projects</span>

                <span className={`${styles.part_title_extra_text} ${styles.left_absolute_extra}`}><span className={styles.rounded_fill}>first</span>
                    {' '}3/2014{' '}</span>
                <span className={`${styles.part_title_extra_text} ${styles.right_absolute_extra}`}><span className={styles.rounded_fill}>last</span>
                    {' '}5/2021{' '}</span>
            </div>

            <div className={styles.part_context}>

                <div className={styles.sec_middle}></div>

                <div className={`${styles.sec_context} ${showProjects ? '' : styles.hide}`}>

                    {list.map(project => {
                        return (
                            <div className={styles.project_container} id={project.slug}>
                                {getVideo(getSelectedItem(project.title)) || getImage(getSelectedItem(project.title)) ? 
                                    <div className={styles.project_images}>
                                        <div className={styles.image_view} style={{ height: isInBigViewMode(project.title) && !getVideo(getSelectedItem(project.title)) ? "550px" : "225px", backgroundImage: getVideo(getSelectedItem(project.title)) ? "" : "url(" + getImage(getSelectedItem(project.title)).replaceAll(" ", "%20") + ")" }}>
                                            {getVideo(getSelectedItem(project.title)) ? (
                                                <video controls>
                                                    <source src={getVideo(getSelectedItem(project.title))} type="video/mp4" />
                                                    Your browser does not support the video.
                                                </video>
                                            ) : (
                                                isInBigViewMode(project.title) ? 
                                                <div className={styles.scale_down} onClick={() => changeBigViewMode(project.title, false)}></div> : 
                                                <div className={styles.full_screen} onClick={() => changeBigViewMode(project.title, true)}></div>
                                            )}
                                            <div className={styles.open} onClick={() => openInNewTab(getVideo(getSelectedItem(project.title)) ? getVideo(getSelectedItem(project.title)) : getImage(getSelectedItem(project.title)) )}></div>
                                        </div>
                                        <div className={styles.image_list}>
                                            {project.videos?.map((video) => {
                                                return <img onClick={() => changeSelectedItem(project.title, video)} className={getSelectedItem(project.title) == video ? `${styles.video_image} ${styles.selected}` : `${styles.video_image}`} src={require('../../assets/images/video-icon.png').default}></img>
                                            })}
                                            {project.images.map((image) => {
                                                return <img onClick={() => changeSelectedItem(project.title, image)} className={getSelectedItem(project.title) == image ? styles.selected : undefined} src={getImage(image)}></img>
                                            })}
                                        </div>
                                    </div>
                                : <></>
                                }
                                <div className={`${styles.project_sides} ${styles.project_side_one} ${styles.float_right}`}>
                                    <div className={styles.project_title}>{project.title}</div>
                                    <div className={styles.project_description}>{project.dec}</div>
                                    <div className="clear"></div>
                                    <div className={styles.project_context}>
                                        <div className={styles.project_parts}>
                                            <ul>
                                                {project.items.map(item => <li><span>{item}</span></li>)}
                                            </ul>
                                        </div>
                                    </div>
                                </div>

                                <div className={`${styles.project_sides} ${styles.project_side_two} ${styles.float_left}`}>
                                    <span className={styles.project_ball}><span className={styles.project_ball_inner}></span></span>

                                    <div className={styles.project_info}>
                                        <div className={`${styles.project_time} ${styles.project_info_text}`}>{project.startTime}</div>
                                        <div className={styles.project_info_text}>
                                            {project.technologies.map(tech => <><span className={styles.project_type}>{tech}</span>{' '}</> )}
                                        </div>
                                        <div className={styles.project_info_text}>{' '}for{' '} 
                                            {project.companies.map(company => <><span className={styles.project_for_who}>{company}</span>{' '}</> )}
                                        </div>
                                    </div>
                                </div>

                                <div className="clear"></div>
                            </div>
                        )
                    })}

                </div>

                { showProjects == false ?
                (
                    <div className={styles.projects_alert_view} onClick={() => showProjectList()}>
                        <a className={styles.alert} href="/" target="_blank" onClick={(e) => e.preventDefault()}>
                            <div className={styles.content}>
                                <div className={styles.title}>Show Project List</div>
                                <span className={styles.dec}>some of latest projects and large-scale projects are not included</span>
                            </div>
                            <div className={styles.icon}>
                                <i className="material-icons">expand_more</i>
                            </div>
                        </a>
                    </div>
                ) : (<></>)
                }
                
            </div>
        </div>
    )

}