export function importAll(r: __WebpackModuleApi.RequireContext) {
    let res: {[key: string]: any} = {};
    r.keys().map((item: any) => { res[item.replace('./', '')] = r(item); });
    return res;
}

export function openInNewTab(url: string) {
    const win: any = window.open(url, '_blank');
    win.focus();
}

export function scrollTo(slug: string) {
    if (!slug) return;
    window.document.querySelector('#'+slug)!.scrollIntoView();
}