import { WorkHistoryModel } from '../../apis/models/work-history';
import styles from './style.module.css';
import { scrollTo } from '../../helpers/utility';
import { useDispatch, useSelector } from 'react-redux';
import { ActionMain } from '../../actions';

export function WorkHistory({ list }: { list: WorkHistoryModel[] }) {

    const dispatch = useDispatch();
    const showProjects = useSelector((state: any) => state.Main.showProjects);

    const goTo = (slug: string, section: string) => {
        if (!showProjects && section == "projects")
            dispatch(ActionMain.showProjects(true))

        setTimeout(() => {
            scrollTo(slug)
        }, 100)
    }

    return (
        <div className={`${styles.work_history} ${styles.parts} ${styles.bottom_part}`} id="work_history">

            <div className={styles.part_title_container}>
                <span className={styles.part_title}>Work History</span>
            </div>

            <div className={styles.part_context}>

                {list.map(model => {
                    return (
                        <div className={styles.work_container}>
                            <div className={styles.line}><i className="material-icons">code</i></div>
                            <div className={styles.job_title}><span>{model.title}</span></div>
                            <div className={styles.company_name}>{model.company}</div>
                            <div className={styles.time_range}><span className={styles.time}>{model.startTime}</span> to <span className={styles.time}>{model.endTime}</span></div>
                            <div className={styles.job_description}>
                                {model.description?.split("\n").map(x => {
                                    return (<span>{x}<br/></span>)
                                })}
                            </div>
                            <div className={styles.job_projects}>
                                <div className={styles.job_projects_title}>Some of Projects:</div>
                                {model.projects?.map(x => {
                                    return (<div className={styles.job_projects_item}>
                                        <span className={`${styles.title} ${x.slug ? styles.clickable : ''}`} onClick={() => goTo(x.slug, "projects")}>{x.title}</span> <span className={styles.dec}>{x.dec}</span> <span className={styles.role}>role: {x.role}</span><br/>
                                    </div>)
                                })}
                            </div>
                        </div>
                    )
                })}

            </div>
        </div>
    )

}