import { useEffect, useState } from 'react';
import styles from './style.module.css';


export const SideBox = () => {

    const [selectedItem, setSelectedItem] = useState("brief");
    const items = [
        { title: "brief", slug: "brief" },
        { title: "in action", slug: "in_action" },
        { title: "work history", slug: "work_history" },
        { title: "projects", slug: "projects" },
    ]

    const changeScrollOffset = (slug: string) => {
        let offset = (document.querySelector("#" + slug) as any).offsetTop;
        window.scrollTo({
            behavior: "smooth",
            top: offset - 50
        })
    }

    const handleScroll = () => {
        const doc = window.document.body;
        const scrollTop = (window.pageYOffset || doc.scrollTop)  - (doc.clientTop || 0);
        console.log(scrollTop);
        
        changeMenuState(scrollTop);
    }

    const changeMenuState = (scrollTop: number) => {
        let selectedItem = "brief";

        items.forEach(item => {
            let offset = (document.querySelector("#" + item.slug) as any).offsetTop;
            offset = offset - 100;
            if (offset < scrollTop) selectedItem = item.slug;
        });
        
        setSelectedItem(selectedItem);
    }

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
    }, [])

    return (
        <div className={styles.side_box} id="side_box">
            {items.map(item => {
                return (<div onClick={() => changeScrollOffset(item.slug)} className={selectedItem == item.slug ? `${styles.side_box_item} ${styles.active}` : `${styles.side_box_item}`}><div className={styles.side_box_item_title}>{item.title}</div></div>)
            })}
        </div>
    )    
}