import styles from './style.module.css';
import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { ActionMain } from '../../actions';
import { AboutMe } from "../../components/about-me";
import { WorkHistory } from '../../components/work-history';
import { Projects } from '../../components/projects';
import { Skills } from '../../components/skills';
import { SideBox } from '../../components/side-box';
import { PDFView } from '../../components/pdf-view';

function MainPage() {

    const dispatch = useDispatch();

    const workHistory = useSelector((state: any) => state.Main.workHistory);
    const projects = useSelector((state: any) => state.Main.projects);
    const companies = useSelector((state: any) => state.Main.companies);
    

    useEffect(() => {
        dispatch(ActionMain.getWorkHistory())
        dispatch(ActionMain.getProjects())
        dispatch(ActionMain.getCompanies())
    }, [])

    return (
        <div className={styles.container}>
            <SideBox></SideBox>
            <div className={styles.inner_container}>
                <AboutMe></AboutMe>
                <PDFView></PDFView>
                <Skills list={projects}></Skills>
                <WorkHistory list={workHistory}></WorkHistory>
                <Projects list={projects} companies={companies}></Projects>
            </div>
        </div>
    );
}

export default MainPage;